*,
*::before,
*::after {
  box-sizing: border-box;
}
* {
  margin: 0;
  padding: 0;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
button {
  cursor: pointer;
  border: none;
}
ul,
ol {
  list-style: none;
  margin: 0;
  padding-left: 0;
}
body {
  font-family: 'Montserrat', 'Inter', sans-serif;
}
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(46, 47, 66, 0.4);
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
    visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}
/* Stylizacja okna modalnego */
.modal {
  /* PoczÄ…tkowo modal jest ukryty */
  position: absolute;
  top: 50%;
  left: 50%;
  padding-left: 18px;
  padding-right: 18px;
  transform: translate(-50%, -50%) scale(1);
  max-width: 300px;
  width: 90%;
  min-height: 300px;
  max-height: 100%;
  background: white;
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.14), 0px 1px 3px rgba(0, 0, 0, 0.12),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  padding: 0;
  overflow: auto;
}
/* Kontent modalny */
.modal-content {
  background-color: #FEFEFE;
  margin: auto;
  padding: 20px;
  border: 1px solid #888;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.img-container {
  padding-top: 20px;
}
/* Stylizowanie przyciskÃ³w zamkniÄ™cia */
.modal-close-button {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 14px;
  right: 14px;
  width: 16px;
  height: 16px;
  background-color: transparent;
  padding: 0;
  cursor: pointer;
  border: none;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    border 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}
.icon-close {
  transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
  stroke: black;
}
/* Stylizacja okÅ‚adki filmu */
.cover-image {
  border-radius: 5px;
  width: 240px;
  height: 357px;
  text-align: center;
  justify-content: center;
}
.title-modal {
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  font-size: 20px;
  color: #000;
  margin: 20px;
}
.film-details-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
}
.film-details-style {
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33333;
  color: #000;
  margin: 4px 0;
}
.film-details {
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33333;
  color: #8C8C8C;
}
.text-modal {
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.66667;
  color: #000;
  margin-top: 10px;
  text-align: justify;
}
/* Kontener dla przyciskÃ³w */
.modal-buttons {
  display: flex;
  gap: 20px;
  justify-content: center;
  margin: 14px;
}
/* Przyciski */
.modal-buttons button {
  font-size: 12px;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  line-height: 1.33333;
  text-transform: uppercase;
  cursor: pointer;
  background-color: transparent;
  border: 1px solid black;
  border-radius: 5px;
  color: #000;
  width: 110px;
  height: 44px;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.modal-buttons button:hover {
  background: #FA6F54;
  color: white;
  border: none;
}
//----------Tutaj trzeba dodaÄ‡ jeszcze trzcionki -------------
.container {
  max-width: 320px;
  margin: 0 auto;
}
@media screen and (min-width: 768px) {
  .container {
    width: 100%;
    max-width: 768px;
    margin: auto;
  }
}
@media screen and (min-width: 1280px) {
  .container {
    width: 100%;
    max-width: 1280px;
    margin: auto;
  }
}